.container{
    widows: 100vw;
    height: calc(100vh - 12vw);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 11.45vw;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 12vw;
}

.content{
    display: flex;
    height: auto;
    margin-bottom: 100px;
}

.lhs{
    margin-right: 20px;
}

.loginCard{
    width: 42.5vw;
    height: 17.4;
    padding: 30px;
    box-shadow: 0px 0px 10px #00000029;
    margin-bottom: 42px;
}

.cardTitle{
    font-size: 20px;
    line-height: 24px;
}

.inputFields{
    display: flex;
}

input{
    border-radius: 5px;
    border: 1px solid #707070;
    height: 54px;
    margin: 10px 0;
    flex: 1;
    font-size: 14px;
    color: #888888;
    padding: 5px;
}

.mr_10{
    margin-right: 10px;
}

.slotsContainer{
    display: flex;
    flex-wrap: wrap;
}

.slotCard{
    width: 17.54vw;
    height: 4vw;
    border: 1px solid #707070;
    margin-bottom: 15px;
    margin-right: 20px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slotsContainer{
    margin-top: 40px;
}

.buyNow{
    width: 100%;
    border: 1px solid #084C95;
    border-radius: 5px;
    height: 43px;
    background-color: #fff;
    margin: 15px 0 30px;
    color: #084C95;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.paymentDetailsCard{
    width: 27vw;
    box-shadow: 0px 0px 6px #00000029;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 20px;
}

.courseDesc{
    font-size: 14px;
    opacity: .5;
    border-bottom: 1px solid #707070;
}

.courseSubTitle{
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
}

.activeSlot{
    background-color: #084C95;
    color: #fff;
}

.borderNone{
    border-bottom: none;
}