.container{
    background: #fff;
    box-shadow: 0px 3px 6px #00000029;
    height: 7vw;
    width: 100vw;
    padding: 0 11.45vw;
    position: fixed;
    top: 0;
    z-index: 10000;
    font-family: sans-serif;
}

.content{
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: space-between;
}

.logo{
    color: #325E9C;
    font-size: 24px;
    line-height: 29px;

}

.links{
    display: flex;
}

.linkItem{
    height: 100%;
    width: 64px;
    color: #333;
    margin: 0;
    font-size: 14px;
    font-weight: 600;
}